import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LexicalClusterCenter } from './../models/LexicalClusterCenter';

export interface IReq {
  lexicalclustercenter: boolean;
  words: Array<string>;
}

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  constructor(
    private http: HttpClient
  ) {}

  getLexicalVectorsByWords(message: IReq): void {
    this.http.post('/word2vec/similar', {"word": "гончар"}).subscribe((response: any) => console.log(response));
    // this.http.post('http://icybcluster.org.ua:34150/word2vec/api/v1.0/query', message).subscribe((response: any) => console.log(response));
  }
}
